import service from "@/uilt/request"

/**
 * 
 * @param {页面索引} page 
 * @param {页面大小} limit 
 * @param {订单状态} state 
 * @param {订单类型} orderType 
 */
export function orderList(page,limit,userid,type)
{
    return service.get("/api/orders/getOrder",{
        params:{
            userid,
            page,
            limit,
            type
        }
    });
}
/**
 * 创建用户订单
 * @param {创建用户订单} obj 
 */
 export function orderCreate(obj){

    return service.post("/api/orders",{
        data:obj
    });
 }

 /**
  * 订单删除
  * @param {删除订单}}} id 
  */
 export function DleteOder(id){
     return service.get("/api/orders/"+id);
 }
 
